import React from "react";

export default function CookieClicker() {
  const grandmasMakeCookies = () => {
    setScore((prevScore) => prevScore + 1 * grandmas);
  };

  useInterval(() => {
    // Your custom logic here
    grandmasMakeCookies();
  }, 1000);

  const [score, setScore] = React.useState(0);
  const [grandmas, setGrandmas] = React.useState(0);
  const [grandmasCost, setGrandmasCost] = React.useState(10);
  const getCookie = () => {
    setScore((prevScore) => prevScore + 1);
  };

  ////
  //this.interval = setInterval(() => this.setState({ time: Date.now() }), 1000);

  const getGrandma = () => {
    if (score >= grandmasCost) {
      setGrandmas((prevGrandmas) => prevGrandmas + 1);
      setScore((prevScore) => prevScore - grandmasCost);
      setGrandmasCost((prevCost) => prevCost * 1.1);
    }
  };

  return (
    <div className="App">
      <h1>Cookie Clicker</h1>
      cookies: {Math.round(score * 100) / 100} <br />
      <button onClick={getCookie}>get a cookie </button>
      <br />
      <button onClick={getGrandma}>
        buy a grandma (costs {Math.round(grandmasCost * 100) / 100} cookies)
      </button>
      <br />
      Current grandmas: {grandmas}
    </div>
  );
}

function useInterval(callback, delay) {
  const savedCallback = React.useRef();

  // Remember the latest function.
  React.useEffect(() => {
    savedCallback.current = callback;
  }, [callback]);

  // Set up the interval.
  React.useEffect(() => {
    function tick() {
      savedCallback.current();
    }
    if (delay !== null) {
      let id = setInterval(tick, delay);
      return () => clearInterval(id);
    }
  }, [delay]);
}
